import React, {useState, useEffect} from 'react';
import '../page.css';
import {AiOutlineClockCircle} from 'react-icons/ai';
import { FiSend} from 'react-icons/fi';
import {IoEarth} from 'react-icons/io5';
import {HiOutlineMail} from 'react-icons/hi';
import {Alert, Button} from 'react-bootstrap';
import {Slide} from 'react-reveal';
import {useDispatch, useSelector} from 'react-redux';
import {postContact} from '../../store/actions/user';
import LoadingBox from '../util/LoadingBox';
import MessageBox from '../util/MessageBox';

function Contact(){
const dispatch = useDispatch();
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [message, setmessage] = useState("");

  const contactpost = useSelector((state)=>state.contactpost);
  const {loading, success, errors} = contactpost;

  const clearForm=()=>{
    setname("");
    setemail("");
    setmessage("");
  }
  
  const handleSubmit=(e)=>{
    e.preventDefault();
    dispatch(postContact(name, email,message));
    clearForm();
  }

  return (
    <div className='contact-section'>
    <div className='section-title'>
      <Slide top><h1>Contact</h1></Slide>
      <span>Leave Me A Message</span>
      </div>

     <div className='contact-container'>

          <Slide bottom>
          <div className='contact-details'>
           <IoEarth className='contact-icons'/>
           <h4>Website</h4>
           <span><a style={{textDecoration: "none", color: "#666666", fontFamily: "Open sans"}} href='https://sachinnimshan.herokuapp.com'>Visit My Web</a></span>
           </div>
          </Slide>

          {/* <div className='contact-details'>
           <IoCallOutline className='contact-icons'/>
           <h4>Call Me</h4>
           <span>+94 77 9012 655</span>
          </div> */}
         

        <Slide top>
        <div className='contact-details'>
           <HiOutlineMail className='contact-icons'/>
           <h4>Email</h4>
           <span>sachinnimshan@gmail.com</span>
         </div>
        </Slide>

         <Slide bottom>
         <div className='contact-details'>
           <AiOutlineClockCircle className='contact-icons'/>
           <h4>Available Hours</h4>
           <span>Monday - Friday</span>
           <span>9:00AM - 05:00PM</span>
         </div>
         </Slide>

        <Slide top>
        <div className='contact-form-container'>
            <h3 className='form-title'>Leave a message</h3>
            {loading && (<LoadingBox></LoadingBox>)}
            {errors && (<MessageBox>{errors}</MessageBox>)}
            {success && (<Alert variant='success'>Your message posted</Alert>)}
            <form className='form-container' onSubmit={handleSubmit}>
            <div className='form-group'>
                <label className='form-lbl'>Your Name</label>
                <input type='text' value={name} onChange={(e)=> setname(e.target.value)}/>
            </div>
            <div className='form-group'>
                <label className='form-lbl'>Email Address</label>
                <input type='text' value={email} onChange={(e)=> setemail(e.target.value)}/>
            </div>
            <div className='form-group'>
                <label className='form-lbl'>Your Message</label>
                <textarea type='text' value={message} onChange={(e)=> setmessage(e.target.value)}/>
            </div>
            <Button type='submit' size='lg' variant='primary'>Send Message</Button>
            </form>
        </div>
        </Slide>


       </div>

</div>
  )
}

export default Contact;